/*======================================
                Open Sans
========================================*/

/* open-sans-regular - latin_cyrillic */
@font-face {
    font-family: 'Open Sans';
    font-style: normal;
    font-weight: 400;
    src: url('./assets/fonts/open-sans/open-sans-v16-latin_cyrillic-regular.eot'); /* IE9 Compat Modes */
    src: local('Open Sans Regular'), local('OpenSans-Regular'),
         url('./assets/fonts/open-sans/open-sans-v16-latin_cyrillic-regular.eot?#iefix') format('embedded-opentype'), /* IE6-IE8 */
         url('./assets/fonts/open-sans/open-sans-v16-latin_cyrillic-regular.woff2') format('woff2'), /* Super Modern Browsers */
         url('./assets/fonts/open-sans/open-sans-v16-latin_cyrillic-regular.woff') format('woff'), /* Modern Browsers */
         url('./assets/fonts/open-sans/open-sans-v16-latin_cyrillic-regular.ttf') format('truetype'), /* Safari, Android, iOS */
         url('./assets/fonts/open-sans/open-sans-v16-latin_cyrillic-regular.svg#OpenSans') format('svg'); /* Legacy iOS */
  }
  /* open-sans-italic - latin_cyrillic */
  @font-face {
    font-family: 'Open Sans';
    font-style: italic;
    font-weight: 400;
    src: url('./assets/fonts/open-sans/open-sans-v16-latin_cyrillic-italic.eot'); /* IE9 Compat Modes */
    src: local('Open Sans Italic'), local('OpenSans-Italic'),
         url('./assets/fonts/open-sans/open-sans-v16-latin_cyrillic-italic.eot?#iefix') format('embedded-opentype'), /* IE6-IE8 */
         url('./assets/fonts/open-sans/open-sans-v16-latin_cyrillic-italic.woff2') format('woff2'), /* Super Modern Browsers */
         url('./assets/fonts/open-sans/open-sans-v16-latin_cyrillic-italic.woff') format('woff'), /* Modern Browsers */
         url('./assets/fonts/open-sans/open-sans-v16-latin_cyrillic-italic.ttf') format('truetype'), /* Safari, Android, iOS */
         url('./assets/fonts/open-sans/open-sans-v16-latin_cyrillic-italic.svg#OpenSans') format('svg'); /* Legacy iOS */
  }
  /* open-sans-700 - latin_cyrillic */
  @font-face {
    font-family: 'Open Sans';
    font-style: normal;
    font-weight: 700;
    src: url('./assets/fonts/open-sans/open-sans-v16-latin_cyrillic-700.eot'); /* IE9 Compat Modes */
    src: local('Open Sans Bold'), local('OpenSans-Bold'),
         url('./assets/fonts/open-sans/open-sans-v16-latin_cyrillic-700.eot?#iefix') format('embedded-opentype'), /* IE6-IE8 */
         url('./assets/fonts/open-sans/open-sans-v16-latin_cyrillic-700.woff2') format('woff2'), /* Super Modern Browsers */
         url('./assets/fonts/open-sans/open-sans-v16-latin_cyrillic-700.woff') format('woff'), /* Modern Browsers */
         url('./assets/fonts/open-sans/open-sans-v16-latin_cyrillic-700.ttf') format('truetype'), /* Safari, Android, iOS */
         url('./assets/fonts/open-sans/open-sans-v16-latin_cyrillic-700.svg#OpenSans') format('svg'); /* Legacy iOS */
  }


